	module.exports = {"data":{"site":{"siteMetadata":{"title":"MyBlog"}},"markdownRemark":{"html":"<pre><code class=\"language-c\">void BubbleSort(uint8_t *pt, uint32_t size){\n   uint32_t i,j; uint8_t data,*p1,*p2;\n   for(i=1; i&#x3C;size; i++){\n     p1 = pt; // pointer to beginning\n     for(j=0; j&#x3C;size-i; j++){\n       p2 = p1+1; // p2 points to the element after p1\n       if(*p1 > *p2){\n         data = *p1; // swap\n         *p1 = *p2;\n         *p2 = data;\n       }\n      p1++;\n    }\n  }\n} \n\nvoid main(void){\nBubbleSort(Vbuffer,100); // Vbuffer = &#x26;VBuffer[0]\nBubbleSort(Pbuffer,200); // Pbuffer = &#x26;PBuffer[0]\n} \n</code></pre>","frontmatter":{"title":"Bubble sort using pointers in C","author":"Maximiliano Valencia","date":"20 March, 2018"}}},"pathContext":{"slug":"/posts/bubble_sort_with_pointers_in_c/"}}

